const model = {
  fetch(localStorageKeyName: string) {
    if (localStorageKeyName === 'recordList') {
      return JSON.parse(localStorage.getItem('recordList') as string) || [];
    } else if (localStorageKeyName === 'dataSource') {
      return {
        eat: '',
        live: '',
        water: '',
        play: '',
        medicals: '',
        traffic: '',
        income: '',
        reward: '',
        sideline: '',
        investment: '',
        account: '',
        books: '',
        clothes: '',
        digital: '',
        equipment: '',
        game: '',
        beauty: '',
        pets: '',
        shopping: '',
        living: '',
        social: '',
        sport: '',
        toys: '',
        transport: '',
        snacks: ''
      };
    } else if (localStorageKeyName === 'data1') {
      return JSON.parse(localStorage.getItem('data1') as string) || {
        eat: '',
        live: '',
        water: '',
        play: '',
        medicals: '',
        traffic: ''
      };
    } else if (localStorageKeyName === 'data2') {
      return JSON.parse(localStorage.getItem('data2') as string) || {
        income: '',
        reward: '',
        sideline: '',
        investment: ''
      };
    }
  },
  save(localStorageKeyName: string, data: RecordItem[] | {}) {
    localStorage.setItem(localStorageKeyName, JSON.stringify(data));
  },
  clone(data: RecordItem) {
    return JSON.parse(JSON.stringify(data));
  }
};

export default model;